const upArrow = <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><path d="M233.4 105.4c12.5-12.5 32.8-12.5 45.3 0l192 192c12.5 12.5 12.5 32.8 0 45.3s-32.8 12.5-45.3 0L256 173.3 86.6 342.6c-12.5 12.5-32.8 12.5-45.3 0s-12.5-32.8 0-45.3l192-192z"/></svg>
const tabs = {
    '/background-and-basics':"Background and Basics",
    '/techniques-of-integration':"Techniques of Integration",
    '/multiple-integration':"Multiple Integration",
    '/applications-of-integration':" Applications of Integration",
    '/vector-calculus':"Vector Calculus",
}

export const Header = (props)=> {
    return (
    <>
        <header>
            <div className="header-upper">
                <div className="logo-wrapper">
                    <p id="logo">
                        Hyper Calculus
                    </p>
                    <span id="slogan">
                        Your Integral Calculus Reference
                    </span>
                </div>
                <Link id="about-tab" to="/about-us">About us</Link>
            </div>
        </header>
        <Navigation />
    </>
    )
}

const Navigation = (props) => {
    // Takes <
    //    choose:the tab which is activated at the moment  !AUTO
    const [isExtended, setIsExtended] = useState(false);
    const [isTabsMenuExtended, setIsTabsMenuExtended] = useState(false);
    const [isMenuExtended, setIsMenuExtended] = useState(false);
    const pathName = useLocation().pathname
    let c = Object.keys(tabs).indexOf(pathName)

    const handleTabClick = (event) => {
        setIsTabsMenuExtended(currentState => currentState? !currentState : currentState)
        setIsExtended(currentState => false)
        for (let el of document.getElementsByClassName("tabs")[0].children) {
            el.classList.remove("highlighted")
        }
        event.target.parentElement.classList.add("highlighted")
    };
    const handleClick = (event) => {
        setIsExtended(currentState => {
            if (currentState) return isMenuExtended? true:false;
            else return true
        });
        setIsMenuExtended(currentState => false);
        setIsTabsMenuExtended(currentState => !currentState);
        document.getElementById('contents-menu').classList.remove('extended')
    }
    const handleMenuBtnClick = (event) => {
        setIsExtended(currentState => {
            if (currentState) return isTabsMenuExtended? true:false;
            else return true
        });
        setIsTabsMenuExtended(currentState => false);
        setIsMenuExtended(currentState => !currentState);
        document.getElementById('contents-menu').classList.toggle('extended')
    }
    
    const tabs_ = useMemo(()=>{
            return Object.keys(tabs).map((e,i) => <li key={i}  id={'tab-'+(i+1)} className={c===i? "highlighted":""}>
                                 <Link to={''+e} onClick={handleTabClick}>
                                     {tabs[e]}
                                 </Link>
                             </li>
        )
    },[])
    return (
    <nav className={`${isExtended?"extended":""}`}>
        <div className="nav__btns">
            <button className="toggle-btn" id="toggle-tabs-btn" onClick={handleClick}>{bars} Topics</button>
            <button className="toggle-btn" id="toggle-menu-btn" onClick={handleMenuBtnClick}>{isMenuExtended ? upArrow : menuBars}</button>
        </div>
        <ul className={`tabs ${isTabsMenuExtended?"extended":""}`}>
            {[...tabs_]}
        </ul>
    </nav>
    )
}

