import React from 'react'
import _ from 'underscore'

import {useEffect, useMemo, useState, useId ,Suspense} from 'react'
import {MathJaxContext, MathJax} from 'better-react-mathjax'
import {Link, useLocation, matchRoutes} from 'react-router-dom'
import {HashLink} from 'react-router-hash-link'

const PATH1 = './assets/manim/'
const PATH2 = './assets/'
const PATH3 = './assets/videos/'
const cTypes = {
    't': ['Theorem', 'theorem'],
    'e': ['Example', 'example'],
    's': ['Solving stratigy', 'stratigy'],
    'n': ['Notes', 'note'],
    'p': ['Proof', 'proof'],
    'd': ['Definition', 'definition'],
    'r': ['Rule', 'rule'],
    'further' : ['Further knowledge', 'further'],
}
const tabs = [
    '/background-and-basics',
    '/techniques-of-integration',
    '/multiple-integration',
    '/applications-of-integration',
    '/vector-calculus',
]

const scrollWithOffset = (el) => {
    const yCoordinate = el.getBoundingClientRect().top + window.pageYOffset;
    const yOffset = -50; 
    window.scrollTo({ top: yCoordinate + yOffset, behavior: 'smooth' }); 
}

export function Chapter(props) {
    window.n = props.n
    useEffect(()=>{console.log("LOADED");window.n = props.n},[props.n])
    let preContentNav = <ContentNav />
    return (
        <>
            {preContentNav}
            <div className={'content-wrapper n'+ props.n}>
                <h2>
                    <span className='before'>{props.n}</span>
                    {props.name}
                </h2>
                <MathJaxContext config={CONFIG}>
                <MathJax hideUntilTypeset='first'>
                    {props.children}
                </MathJax>
                </MathJaxContext>
            </div>
            {/* <Navigation choose={props.n}/> */}
        </>
    )
}

export const SM = (props)=> {
    // Takes < 
    //      h:header  !! , !UNIQUE, 
    const heading = props.h
    const id = heading.toLowerCase().replaceAll(' ','_')
    return (
        <section className="section1" id={id} name={heading}>
            <h3>{heading}</h3>
            {props.children}
        </section>
    )
}

export const S = (props)=> {
    // Takes < 
    //      h:header  !! , !UNIQUE,
    const heading = props.h
    const id = heading.toLowerCase().replaceAll(' ','_')
    return (
        <section className="section2" id={id} name={heading}>
            <h4><span>&#8811; </span> {heading}</h4>
                {props.children}
        </section>
    )
}

export const C = (props)=>{
    // Takes < 
    //    h:title             #= ""
    //    t:type {cTypes}     #= "t"
    const id = _.uniqueId()
    const title = props.h? ': '+props.h : ':';
    let config = ['Theorem:', 'theorem'];
    let t = 't';
    if (props.t in cTypes) {
        t = props.t
        config = cTypes[props.t];
    }
    console.log()

    return (
        <div className='c-holder' id={id} name={t}>
            <div className={config[1] + ' c'}>
                <h5 className='title'>
                    <span>{config[0]}</span>{title}
                </h5>
                <div className="content">
                    {props.children ? props.children : "... ... ...."}
                </div>
            </div>
        </div>
    )
}

export const Table = (props)=> {
    return (
        <div className='math-table-container'>
            <table className="math-table">
                <thead>
                    {props.children[0]}
                </thead>
                <tbody>
                    {props.children.slice(1)}
                </tbody>
            </table>
        </div>
    )
}

export const U1 = (props)=> {
    return (
        <ul className='list1'>
            {props.children}
        </ul>
    )
}

export const A = (props) => {
    // Takes < 
    //      r:route {1,2,3,4,5}           #= 3, 
    //          1: /background-and-basics'
    //          2: /techniques-of-integration'
    //          3: /multiple-integration'
    //          4: /vector-calculus'
    //          5: /applications-of-integration'
    //      id:                           !!
    const id = props.id.toLowerCase().replaceAll(' ','_')
    const route = props.r ? tabs[props.r - 1]: tabs[2]
    return (
        <HashLink to={route + '#' + id} className="local-reference">
            {props.children}
        </HashLink>
    )
}

export const Mimg = (props) => {
    // Takes < 
    //      h:title, caption  #= "", 
    //      t:path type {'g',''}   #= "",
    //          'g': To take source from assets directory (PATH2)
    //          '' : To take source from assets/manim directory (PATH1)
    //      p:path            !!,
    //      alt: alternative text in case of missed image  #="",
    const path = props.t === 'g'?  PATH2 + props.p : PATH1 + props.p;
    const alt = props.alt? props.alt: "";
    return (
        <figure className='img-wrapper'>
            <Suspense fallback={<Loading />}>
                <img src={require('' + path)} alt={alt}/> {/*Here we added '' so we don't get "can't fine module" error*/}
            </Suspense>
            <figcaption>{props.h}</figcaption>
        </figure>
    )
}

export const Mvid = (props) => {
     // Takes < 
    //      h:title, caption  #= "", 
    //      p:path            !!,
    //      e:extension       #= "video/mp4",
    const path = PATH3 + props.p
    const extension = props.e? props.e : "video/mp4"
    return( <div className='video-wrapper'>
        <Suspense fallback={<Loading />}>
            <video width="100%" height="auto" controls >
                <source src={require(""+path)} type={extension}/>
            </video>
            <p className='vid-disc'>
                {props.h}
            </p>
        </Suspense>
    </div>)
}

export const ContentNav = (props) => {
    // Takes <
    //    sm:main sections list     !AUTO
    const location = useLocation().pathname;
    const SMElements = props.sm;
    let order = {'r':0, 't':0, 's':0, 'n':0, 'p':0, 'e':0, 'd':0};
    let nKey = 0
    const [ordered_, setOrdered] = useState([])

    useEffect(()=>{
        console.log("Initialized again")
        let ordered = []
        for (let parent1 of document.getElementsByClassName("section1")) {
            nKey ++;
            ordered.push(
                <li className={'content-main-section'} key={nKey}>
                    <HashLink smooth to={`#${parent1.id}`} scroll={el => scrollWithOffset(el)}>
                        {parent1.getAttribute('name')}
                    </HashLink>
                </li>
            )
            for (let parent2 of parent1.getElementsByClassName("section2")) {
                nKey ++;
                ordered.push(
                    <li className={'content-section'} key={nKey}>
                        <HashLink smooth to={`#${parent2.id}`} scroll={el => scrollWithOffset(el)}>
                            {parent2.getAttribute('name')}
                        </HashLink>
                    </li>
                );
                for (let child of parent2.getElementsByClassName("c-holder")) {
                    let type = child.getAttribute('name');
                    let id = child.id;
                    nKey ++;
                    if (type !== 'further') {
                        order[type] ++;
                        ordered.push(
                            <li className={`content-container`} key={'c' + id}>
                                <HashLink smooth to={`#${id}`} scroll={el => scrollWithOffset(el)}>
                                    {cTypes[type][0]}, {order[type]}
                                </HashLink>
                            </li>
                        )
                    } else {
                        ordered.push(
                            <li className={`content-container`} key={'c' + id}>
                                <HashLink smooth to={`#${id}`} scroll={el => scrollWithOffset(el)}>
                                    further knowledge
                                </HashLink>
                            </li>
                        )
                    }
                }
            }
        }
        setOrdered(ordered)
    },[])
    return (
        <aside className='contents-menu' id="contents-menu">
            <ul>
                {[...ordered_]}
            </ul>
        </aside>
    )
}

export const Loading = ()=>{
    return (
        <div className="loading-screen">
            <div className='loading-spinner' />
        </div>
    )
}

export const CONFIG = {
    // The MathJax configuration block
    jax: ["input/TeX","output/SVG"],/*, "output/PreviewHTML"*/
    svg: {
        scale: 1,                      // global scaling factor for all expressions
        minScale: .5,                  // smallest scaling factor to use
        mtextInheritFont: false,       // true to make mtext elements use surrounding font
        merrorInheritFont: true,       // true to make merror text use surrounding font
        mathmlSpacing: false,          // true for MathML spacing rules, false for TeX rules
        skipAttributes: {},            // RFDa and other attributes NOT to copy to the output
        exFactor: .5,                  // default size of ex in em units
        displayAlign: 'center',        // default for indentalign when set to 'auto'
        displayIndent: '0',            // default for indentshift when set to 'auto'
        fontCache: 'local',            // or 'global' or 'none'
        localID: null,                 // ID to use for local font cache (for single equation processing)
        internalSpeechTitles: true,    // insert <title> tags with speech content
        titleID: 0                     // initial id number to use for aria-labeledby titles
    },
    tex: {
      inlineMath: [["$", "$"],['"','"']],
      displayMath: [["$$", "$$"]],
      // packages: ['amsmath','amssymb','float','base', 'require','ams','mathtools']
      packages: {'[+]': ['mathtools','amssymb']},
    },
    startup: {
      typeset: false
    },
    options: {
        enableMenu: true,
    },
};